.testimonios{
    padding: 40px 40px 80px;
  }
  
  .testimonios .carousel{
    max-width: 800px;
  }
  
  .carousel-item{
    height: 500px;
  }
  
  .carousel-item .container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .testimonio-imagen{
    height: 150px;
    width: 150px;
    margin: 10px 10px 20px;
  }
  
  .testimonio-texto{
    max-width: 70%;
    font-size: 20px;
    text-align: center;
    font-family: 'Share Tech Mono', monospace;
  }
  
  .testimonio-info{
    font-weight: bold;
    text-align: center;
  }
  
  .testimonio-info p{
    margin-bottom: 0;
  }
  
  .testimonio-info .cliente{
    font-style: 1.2rem;
  }
  
  .testimonio-info .cargo{
    font-size: 0.9rem;
    color: #656060;
  }