/* Estilos generales */
section{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1{
  font-size: 2.5rem;
  font-family: 'Bacasime Antique', serif;
}

.seccion-obscura{
  color:white;
  background-color: #1b1b32;
}

.seccion-clara{
  color: black;
  background-color: white;
}

.seccion-titulo{
  font-size: 2rem;
  padding: 15px 0;
  font-family: 'Bacasime Antique', serif;
}

.seccion-descripcion{
  font-size: 1.2rem;
  color: #584e4e
}

.seccion-texto{
  font-size: 1.2rem;
}

.btn-info{
  font-size: 20px;
  margin: 20px;
}

.texto-negro{
  color: black;
}

.texto-blanco{
  color: white;
}